.gettingStarted,
.paragraph {
  align-self: stretch;
  position: relative;
}
.gettingStarted {
  margin: 0;
  font-size: var(--heading-h3-bold-desktop-size);
  letter-spacing: -0.02em;
  line-height: 40px;
  font-weight: 700;
  font-family: inherit;
  color: var(--neutral-900);
}
.paragraph {
  font-size: var(--paragraph-medium-semibold-size);
  line-height: 24px;
}
.mainFrame {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-5xl);
  gap: var(--gap-xs);
  text-align: left;
  font-size: var(--paragraph-small-semibold-size);
  color: var(--neutral-500);
  font-family: var(--paragraph-xsmall-regular);
}
@media screen and (max-width: 825px) {
  .gettingStarted {
    font-size: var(--font-size-7xl);
    line-height: 32px;
  }
}
@media screen and (max-width: 450px) {
  .gettingStarted {
    font-size: var(--font-size-lgi);
    line-height: 24px;
  }
}
