@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --paragraph-xsmall-regular: Inter;

  /* font sizes */
  --paragraph-xsmall-regular-size: 12px;
  --paragraph-small-semibold-size: 14px;
  --paragraph-medium-semibold-size: 16px;
  --heading-h5-semibold-desktop-size: 24px;
  --font-size-lgi: 19px;
  --heading-h3-bold-desktop-size: 32px;
  --font-size-7xl: 26px;

  /* Colors */
  --neutral-100: #f5f5f5;
  --generic-white: #fff;
  --neutral-500: #737373;
  --primary-500-main: #6366f1;
  --neutral-900: #171717;
  --neutral-200: #e5e5e5;
  --neutral-300: #d4d4d4;

  /* Gaps */
  --gap-29xl: 48px;
  --gap-5xl: 24px;
  --gap-xs: 12px;
  --gap-9xs: 4px;
  --gap-5xs: 8px;

  /* Paddings */
  --padding-xl: 20px;
  --padding-9xs: 4px;
  --padding-5xs: 8px;
  --padding-5xl: 24px;

  /* Border radiuses */
  --br-9xs: 4px;

  /* Effects */
  --drop-shadow-xsmall: 0px 1px 2px rgba(16, 24, 40, 0.04);
}
