@keyframes marquee-text {
  0% {
    transform: translateX(100%);
  }
  to {
    transform: translateX(-100%);
  }
}
.date {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 32px;
  font-weight: 600;
  font-family: inherit;
  opacity: 1;
  white-space: nowrap;
}
.date.animate,
.paragraph.animate {
  animation: 44s linear -22s infinite normal forwards marquee-text;
}
.figmaMasterclass1 {
  text-decoration: underline;
  font-weight: 600;
}
.figmaMasterclass {
  color: var(--primary-500-main);
}
.paragraph {
  align-self: stretch;
  position: relative;
  font-size: var(--paragraph-medium-semibold-size);
  line-height: 24px;
  opacity: 1;
  white-space: nowrap;
  color: var(--neutral-500);
}
.text {
  width: 2191px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  max-width: 304%;
  flex-shrink: 0;
}
.frameIcon,
.label,
.supportingText {
  align-self: stretch;
  position: relative;
}
.frameIcon {
  flex: 1;
  border-radius: var(--br-9xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.label,
.supportingText {
  line-height: 20px;
}
.label {
  font-weight: 600;
  color: inherit;
  text-decoration: none;
}
.supportingText {
  font-size: var(--paragraph-xsmall-regular-size);
  color: var(--neutral-500);
}
.block,
.text1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.text1 {
  align-self: stretch;
  padding: 0 0 0 var(--padding-9xs);
  gap: var(--gap-9xs);
}
.block {
  height: 192px;
  gap: var(--gap-xs);
}
.frameIcon1 {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: var(--br-9xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.structureLayoutsThat,
.understandingAutoLayout {
  align-self: stretch;
  position: relative;
  line-height: 20px;
}
.understandingAutoLayout {
  font-weight: 600;
  color: inherit;
  text-decoration: none;
}
.structureLayoutsThat {
  font-size: var(--paragraph-xsmall-regular-size);
  color: var(--neutral-500);
}
.block1,
.text2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.text2 {
  align-self: stretch;
  padding: 0 0 0 var(--padding-9xs);
  gap: var(--gap-9xs);
}
.block1 {
  height: 192px;
  gap: var(--gap-xs);
}
.frameIcon2 {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: var(--br-9xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.groupAndOrganise,
.workingWithVariants {
  align-self: stretch;
  position: relative;
  line-height: 20px;
}
.workingWithVariants {
  font-weight: 600;
  color: inherit;
  text-decoration: none;
}
.groupAndOrganise {
  font-size: var(--paragraph-xsmall-regular-size);
  color: var(--neutral-500);
}
.text3 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  padding: 0 0 0 var(--padding-9xs);
  gap: var(--gap-9xs);
}
.block2,
.content,
.resources,
.text3 {
  align-items: flex-start;
  justify-content: flex-start;
}
.block2 {
  height: 192px;
  display: flex;
  flex-direction: column;
  gap: var(--gap-xs);
}
.content,
.resources {
  align-self: stretch;
}
.resources {
  display: grid;
  flex-direction: row;
  gap: var(--gap-xs);
  grid-template-columns: repeat(3, minmax(174px, 1fr));
  font-size: var(--paragraph-small-semibold-size);
  color: var(--primary-500-main);
}
.content {
  display: flex;
  flex-direction: column;
  gap: var(--gap-5xl);
  max-width: 100%;
  text-align: left;
  font-size: var(--heading-h5-semibold-desktop-size);
  color: var(--neutral-900);
  font-family: var(--paragraph-xsmall-regular);
}
@media screen and (max-width: 675px) {
  .resources {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(174px, 302px));
  }
}
@media screen and (max-width: 450px) {
  .date {
    font-size: var(--font-size-lgi);
    line-height: 26px;
  }
  .resources {
    grid-template-columns: minmax(174px, 1fr);
  }
}
